/* eslint-disable @typescript-eslint/no-explicit-any */
import { Directive, Input } from '@angular/core';
import {
  ControlValueAccessor, Validator
} from '@angular/forms';
import { FormFieldBase } from './form-base';

@Directive({})
export class InputFormFieldBase
  extends FormFieldBase
  implements ControlValueAccessor, Validator {
  @Input() type: string = 'text';

  constructor() {
    super();
  }

  // --- Template Interaction ---
  // Need methods to bind to input events in the template
  onInput(event: Event): void {
    const target = event.target as HTMLInputElement;

    this.updateValue(target.value); // Use the helper from base class
  }

  onBlur(): void {
    this.handleBlur(); // Use the helper from base class
  }

  dispVals(vals) {
    return JSON.stringify(vals);
  }
}
